/* inter-100 - latin */
@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 100;
  src: url('../assets/fonts/Inter/inter-v2-latin-100.eot'); /* IE9 Compat Modes */
  src: local(''),
  url('../assets/fonts/Inter/inter-v2-latin-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../assets/fonts/Inter/inter-v2-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
  url('../assets/fonts/Inter/inter-v2-latin-100.woff') format('woff'), /* Modern Browsers */
  url('../assets/fonts/Inter/inter-v2-latin-100.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../assets/fonts/Inter/inter-v2-latin-100.svg#Inter') format('svg'); /* Legacy iOS */
}
